@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@800&display=swap);
html{
  font-size: 10px;;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding:0;
  background-color:#073B4C;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.6rem;
}

small {
  font-size: 1.2rem;
}

.container {
  margin: 0 auto;
  max-width: 1000px;
  padding:40px;
}

.title {
  font-family: 'Montserrat', sans-serif;
  font-size: 5rem;
  color: whitesmoke;
  text-align: center;
}

.form {
  display: grid;
}


.label {
  font-family: 'Montserrat', sans-serif;
  font-size: 3rem;
  color: whitesmoke;
  text-align: center;
}

.query {
  font-size: 1.6rem;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border-radius: 20px;
  margin-bottom: 10px;
  border: none;
  box-shadow: 0 10 10 #073B4C;
  font-style: italic;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.button {
    width:100px;
    border: none;
    border-radius: 15px;
    padding:10px;
    background-color: #FFD166;
    cursor:pointer;
    margin: 0 auto;
}

.button:hover {
  background-color: #FFD144;
}


.card-list {
  margin-top: 4rem;
  text-align: center;
  color:white;
}

.card {
  padding: 2rem 4rem;
  margin: 0 2rem;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgba(0,0,0,0.25);
  margin-bottom: 2rem;
  background-color: #f5f5f5;
}

.card--image {
  margin: 0 auto;
}

.card--title {
  margin-bottom: 0.5rem;
  font-size: 3.2rem;
}

.small--text {
  text-transform: uppercase;
  font-weight: 700;
}

@media (min-width:786px) {
  .form {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .query {
    width: 400px;
    margin: 0 20px;
    margin-bottom:0;
  }
  
  .button {
    margin:0;
  }

  .card-list {
    margin-top:45px;
    display:grid;
    grid-template-columns: auto auto auto;
  }

  .card--image {
    margin: 0 auto;
  }
}

*{
  font-family: Montserrat, 'sans-serif';
  color:#343a40;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  margin-top: 40px;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

